.i {
    display: flex;
    height: 110vh;
  }
  
  .i-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .i-right {
    flex: 1 1;
    position: relative;
  }
  
  .i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .i-intro {
    font-size: 30px;
    font-weight: 300;
  }
  
  .i-name {
    font-size: 60px;
  }
  
  .i-title {
    height: 60px;
    overflow: hidden;
  }
  
  .i-title-wrapper {
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
  }
  
  @-webkit-keyframes move {
    25% {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
    }
    50% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    75% {
      -webkit-transform: translateY(-150px);
              transform: translateY(-150px);
    }
    100% {
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  
  @keyframes move {
    25% {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
    }
    50% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    75% {
      -webkit-transform: translateY(-150px);
              transform: translateY(-150px);
    }
    100% {
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  
  .i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;
  }
  
  .i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
  }
  
  .i-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
  }
  
  .i-bg {
    -webkit-clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
  }
  
  @media screen and (max-width: 480px) {
    .i {
      display: flex;
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height:100%;
    }
  
    .i-desc{
      display: flex;
      flex-direction: column;
      margin-top: 2rem;
    }
    
  }
.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.t-icon{
    width: 15px;
    height: 15px;
}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}
